// 收藏
$('.ops-coll').on('click', function () {
  if ($(this).hasClass('active')) {
    $(this).removeClass('active');
  } else {
    $(this).addClass('active');
  }
}); // 点赞

$('.cmt-like').on('click', function () {
  if ($(this).hasClass('active')) {
    $(this).removeClass('active');
  } else {
    $(this).addClass('active');
  }
}); // 禁止底层滚动

var scrollTop = 0;

function bodyfixed(isFixed) {
  var $body = $(document.body);

  if (isFixed) {
    scrollTop = $(document).scrollTop();
    $body.css('overflow', 'hidden').css('position', 'fixed'); // $(document.body).css('top', -scrollTop);
  } else {
    $body.css('overflow', 'inherit').css('position', 'inherit'); // $(document).scrollTop(scrollTop);
  }
} // 评论


function cmtHide() {
  $('.cmt-mask').hide();
  $('.cmt-txta').hide();
  bodyfixed(false);
}

$('.cmt-btn').on('click', function () {
  $('.cmt-mask').show();
  $('.cmt-txta').show();
  $('.cmt-txta .txta').focus();
  bodyfixed(true);
  event.preventDefault(); // alert($('body').height())
  // maty
  // $('.cmt-txta').offset().top;
  // // maty
  // setTimeout(function () {
  // 	alert($('.cmt-txta').offset().top)
  // }, 300);
});
$('.cmt-txta').on('click', '.cancel', function () {
  cmtHide();
});
$('.cmt-mask').on('touchstart', function () {
  cmtHide();
  document.activeElement.blur();
});
$('#txtaId').on('blur keyup input', function () {
  var len = $('#txtaId').val().length;
  $('.txta-num .cur').text(len);

  if (len > 0) {
    $('.cmt-txta .release').addClass('primary');
  } else {
    $('.cmt-txta .release').removeClass('primary');
  }

  ;

  if (len >= 500) {
    $('.txta-num .cur').addClass('danger');
  } else {
    $('.txta-num .cur').removeClass('danger');
  }

  ;
}); // 重置

window.release = function () {
  var val = $('#txtaId').val('');
  var len = val.length - 1;
  $('.txta-num .cur').text(len); // 清空内容

  cmtHide(); // 隐藏弹层

  $('.tab-nav li:last').click(); // 跳转到评论
  // titTop

  var titTop = $('#titTop').offset().top;
  var imgH = $('.video-box').height();
  var tabNav = $('.tab-nav').height();
  var zong = titTop - imgH - tabNav;
  $('html,body').scrollTop(zong);
}; // $('.release').click(function(){
// 	release();
// });
// Tab


$('.tabsBox .tab-nav').on('click', 'li', function (e) {
  $(this).addClass('active').siblings().removeClass('active');
  var index = $(this).index();
  $('.tabsBox .tab-panel .panel-item').hide();
  $('.tabsBox .tab-panel .panel-item:eq(' + index + ')').show();
});